import React from 'react';
import { Route, Switch } from 'react-router-dom'
import "../styles/Header.css";
import losAngeles from '../images/los-angeles.jpg';
import newYork from '../images/new-york.jpg';
import Singapur from '../images/Singapur.jpg';
import Warszawa from '../images/Warszawa.jpg';



export const Header = () => {
    return (
        <>
        <Switch>
             <Route path='/' exact render={() => (
                 <img src={losAngeles} alt="city" />
             )}/>
             <Route path='/products' render={() => (
                 <img src={newYork} alt="city" />
             )}/>
             <Route path='/contact' render={() => (
                 <img src={Singapur} alt="city" />
             )}/>
             <Route path='/admin' render={() => (
                 <img src={Warszawa} alt="city" />
             )}/>
             <Route render={() => (
                 <img src={Warszawa} alt="city" />
             )}/>
        </Switch>
        </>
    )
}
