import React from 'react'

export const Article = ({title, author, text}) => {
    const styles = {
        marginTop: 40,
    }
    const stylesText = {
        marginBottom: 3,
        textTransform: 'uppercase'
    }
    const stylesParafa = {
        display: 'block',
        marginTop: 20,
    }
    const stylesParafaX = {
        display: 'block',
        marginTop: 20,
        fontWeight: 600,
    }
    return (
        <article style={styles}>
            <h3 style={stylesText}>{title}</h3>
            <span style={stylesParafaX}>{author}</span>
            <p style={stylesParafa}>{text}</p>
        </article>
    )
}
