import React, { Component } from 'react';
import '../styles/ContactPages.css';
import {Prompt} from 'react-router-dom'

class ContactPages extends Component {
    state = {
        value: '',
        isEmpty: true
    }
    handleSubmit = (e) => {
        e.preventDefault()
        this.setState({
            value: '',
            isEmpty: true
        })
    }
    handleChange = (e) => {
        if(e.target.value.length > 0) {
            this.setState({
                value: e.target.value,
                isEmpty: false
            })
        } else {
            this.setState({
                value: e.target.value,
                isEmpty: true
            })
        }
    }
    render() { 
        return (
            <div className="contact">
                <form onSubmit={this.handleSubmit}>
                    <h3>Napisz do nas</h3>
                    <textarea onChange={this.handleChange} placeholder="wpisz wiadomości" value={this.state.value}></textarea>
                    <button>Wyślij</button>
                </form>
                <Prompt when={!this.state.isEmpty} message="Masz niewypełniony formularz. Czy chcesz opuścic tę stronę."/>
            </div>
        );
    }
}
 
export default ContactPages;