import React from 'react'
import { Route , Switch } from 'react-router-dom'
import { HomePages } from '../pages/HomePages'
import ProductPages from '../pages/ProductPages'
import  ContactPages  from '../pages/ContactPages'
import { AdminPages } from '../pages/AdminPages'
import { ErrorPages } from '../pages/ErrorPages'
import { LoginPages } from '../pages/LoginPages'
import { ProductListPages } from '../pages/ProductListPages'


export const Page = () => {
    return (
    <>
    <Switch>
        <Route path="/" exact component={HomePages} />
        <Route path="/product/:id" component={ProductPages} />
        <Route path="/products" component={ProductListPages} />
        <Route path="/contact" component={ContactPages} />
        <Route path="/admin" component={AdminPages} />
        <Route path="/login" component={LoginPages} />
        <Route component={ErrorPages} />
    </Switch>
    </>
    )
}
// /:id"  dowolny element
